import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Layout from '@/layout'

const routes = [
  {
    path: '/',
    component:Layout,
    redirect:'/w',
    children:[
      {
        path:'w',
        component: ()=>import('@/views/webList/index'),
        name:'webList',
        meta:{ title:'网站列表',index:'2'}
      }
    ]
  },
  {
    path: '/tool',
    component:Layout,
    redirect:'/tool/base64',
    children:[
      {
        path:'base64',
        component: ()=>import('@/views/tool/base64'),
        name:'base64',
        meta:{ title:'图片转base64',index:'3-1'}
      }
    ]
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router

